<script>
// mounted 钩子函数  这里去请求豆瓣数据
export default {
    name: 'pollenFamilySearch',
    data() {
        return {
            msg: '花粉科属查询',
            pollens: [],
            pollen: {
                name: null,
                family: null
            }
        }
    },
    methods: {
        search: function () {// 根据花粉科属查询
            this.$http.get('http://localhost:8081/search/family', {
                params: this.pollen
            })
                .then(function (response) {
                    this.pollens = response.body.data;
                })
                .catch(function (error) {
                    console.log(error);
                });
        }
    }
}
</script>

