<script>
// mounted 钩子函数  这里去请求豆瓣数据
export default {
    name: 'pollenSearch',
    data () {
        return {
            msg: '花粉特征查询',
            pollens: [],
            lables: []
        }
    },
    methods: {
        search: function () {
            this.$http.get('http://localhost:8081/search/s', {
                params: this.lables
            })
            .then(function (response) {
                this.pollens = response.body.data;
            })
            .catch(function (error) {
                console.log(error);
            });
        }
    }
}
</script>

